.Cookie{
    width: 100%;
    margin: auto;
    height: 250px;
    overflow: scroll;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
}
@media (min-width:500px) and (min-height: 400) {
    .Cookie {
        width: 350px;
        height: 300px;
    }
}
@media (min-width:500px) and (min-height: 401) {
    .Cookie {
        width: 450px;
        height: 400px;
    }
}
    @media (min-width:1000px) and (min-height: 700) {
        .Cookie {
            width: 700px;
            height: 600px;
        }
}